/* Estilos básicos para la aplicación */
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}

.hidden {
  display: none;
}

.formulario {
  text-align: justify;
}

.estiloInputs {
  display: flex;
  justify-content: space-around;
  width: 100%; /* Puedes ajustar el ancho según tus necesidades */
  margin-top: 1rem;
  display: inline-block;
}

.estiloInputs label {
  margin-right: 1rem;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #004d40; /* Un verde más oscuro para un toque sofisticado */
  color: #ffffff;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
  background-size: cover;
}

.intro h1 {
  margin: 0;
  font-size: 2.1em;
  font-weight: 700;
  margin-bottom: 1rem;
}

button {
  padding: 12px 24px;
  font-size: 16px;
  cursor: pointer;
  background-color: #00796b; /* Verde más oscuro */
  color: #ffffff;
  border: none;
  border-radius: 8px;
  margin-top: 20px;
  transition: background-color 0.3s, transform 0.2s;
}

button:hover {
  background-color: #004d40;
  transform: scale(1.05);
}

button:active {
  transform: scale(0.95);
}

.auth {
  padding: 25px;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  max-width: 500px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 2rem;
}

h2 {
  margin-top: 0;
  font-weight: 600;
  color: #004d40; /* Coincide con el color de fondo */
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-top: 15px;
  font-weight: 600;
  color: #333333;
}

input[type="text"],
input[type="number"],
input[type="password"],
input[type="email"],
textarea {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #c0c0c0;
  border-radius: 8px;
  margin-top: 5px;
  width: 100%;
  box-sizing: border-box;
}

textarea {
  resize: vertical;
}

#fileInput {
  display: none;
}

#scanResult {
  margin-top: 20px;
  font-size: 18px;
  font-weight: 600;
  color: #e53935; /* Rojo para resultados negativos */
}

.inputForm {
  margin-top: 1rem;
  width: 10%;
  margin-left: auto;
  margin-right: auto;
  height: 1.7rem;
  font-size: 1.3rem;
  text-align: center;
}

.footer {
  background-color: #004d40;
  color: #ffffff;
  text-align: center;
  padding: 0.5rem;
  position: fixed;
  bottom: 0;
  width: 90%;
  height: 2%;
  font-size: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
